import * as Helpers from '../helpers';
import { DscCredential } from './index';

export abstract class DscType {

    public dependsOn: string;
    public psDscRunAsCredential: DscCredential;

    private _dscResourceId: string = '';
    private _dscResourceType: string = '';

    constructor() {
        let _self: DscType = this;
        let classObject: any = Object.getPrototypeOf(_self);
        let dscResource: string = classObject.constructor.name.replace('Msft', '');
        this._dscResourceType = dscResource;

        this._dscResourceId = Helpers.generateGuid();
    }

    public get Name(): string {
        return `${this._dscResourceType} ${this._dscResourceId}`;
    }

    public get DependanceName(): string {
        return `[${this._dscResourceType}]${this._dscResourceId}`;
    }

    public toString = (): string => {
        let _self: DscType = this;

        let propertyValues: string = '';
        for (let key in _self) {
            if (_self.hasOwnProperty(key) && !key.startsWith('_dsc')) {
                let val: string = _self[key];
                let name: string = `${key.toString().substring(0, 1).toUpperCase()}${key.toString().substring(1)}`;
                switch (typeof val) {
                    case 'function':
                        break;
                    case 'object':
                        let subClassObject: any = Object.getPrototypeOf(val);
                        if (subClassObject.constructor.name === 'DscCredential') {
                            propertyValues += `
                            ${name}   =   ${val.toString()}`;
                        }
                        break;
                    case 'boolean':
                        propertyValues += `
                            ${name}   =   ${val ? '$true' : '$false'}`;
                        break;
                    default:
                        console.log(name);
                        if (name === 'DependsOn' && (val).startsWith('@')) {
                            propertyValues += `
                            ${name}   =   ${val}`;
                            break;
                        }
                        if (name === 'Ensure') {
                            let ensure: string = val.toString() === '0' ? 'Absent' : 'Present';
                            propertyValues += `
                            ${name}   =   "${ensure}"`;
                            break;
                        }
                        propertyValues += `
                            ${name}   =   "${val}"`;

                        break;
                }
            }
        }

        let configurationValue: string = `
                ${this.Name}
                {   ${propertyValues}
                }
            `;
        return configurationValue;
    }
}
